.App {
  text-align: center;
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  font-size: calc(10px + 2vmin);
  color: white;
}

.registration {
  display: flex;
  flex-direction: column;
  height: 60vh;
  width: 20vw;
  justify-content: space-evenly;
}

.input {
  height: 30px;
  border-radius: 5px;
  border-style: none;
  padding: 5px;
  font-size: calc(6px + 1vmin);
}

.button {
  height: 30px;
  border-radius: 5px;
  border-style: none;
  font-size: calc(6px + 1vmin);
}

.message {
  position: absolute;
  top: 0;
  width: 100%;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: brown;
  color: white;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
